<template>
  <div class="content">
    <section class="operaitonContent">
      <div class="formLine">
        <p>输入手牌：</p>
        <input type="text" name id />
        <div class="searchBtn" @click="search">查询</div>
      </div>
      <div class="operationContent">
        <div v-for="(item,index) in operationData" :key="index">
          <van-icon v-show="item.cardType == 2" name="checked" size="20" color="#7F8184" />
          <div class="operationItem">
            <div class="topItem">
              <img v-show="item.sex == 1" src="@/assets/b.png" />
              <img v-show="item.sex == 2" src="@/assets/g.png" />
              <div class="right">
                <div class="topInfo">
                  <p>
                    {{item.num}}
                    <span>{{item.type != 2 ? 'VIP':'' }}</span>
                  </p>
                  <p>{{item.time}}</p>
                </div>
                <p>{{item.state == 1 ? '开牌':item.state == 2 ? '锁定':item.state == 3 ? '挂失':'限额'}}</p>
              </div>
            </div>
            <div class="rightBottomBtn">
              <div class="bottomBtn">取消开牌</div>
              <div class="bottomBtn">手牌挂失</div>
              <div class="bottomBtn">手牌锁定</div>
              <div class="bottomBtn" v-show="item.cardType == 2">取消团队</div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="operationMean">
      <div class="mean" v-for="(item,index) in bottomBtn" :key="index" @click="operationBtn(index)">
        <img :src="item.imgIco" />
        <p>{{item.name}}</p>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      operationData: [],
      bottomBtn: this.getMeanJson.bottomBtn,
      flag: true
    };
  },
  mounted() {
    this.search();
    console.log(JSON.stringify(this.getMeanJson.bottomBtn));
  },
  methods: {
    search() {
      this.flag = !this.flag;
      if (this.flag) {
        this.singleData();
      } else {
        this.teamData();
      }
    },
    singleData() {
      var singleCard = JSON.parse(
        JSON.stringify(this.getRespone.operationData)
      );
      this.operationData = singleCard.filter(e => {
        return e.cardType === 1;
      });
    },
    teamData() {
      var teamCard = JSON.parse(JSON.stringify(this.getRespone.operationData));
      this.operationData = teamCard.filter(e => {
        return e.cardType === 2;
      });
    },
    operationBtn(index) {
      var bottomBtnJson = this.getMeanJson.bottomBtn;
      bottomBtnJson.forEach((e, idx) => {
        bottomBtnJson[idx].dataName = "";
        if (index == idx) {
          e.dataName = e.tName;
        } else if (e.dataName == e.tName) {
          e.dataName = e.name;
        }
      });
      this.bottomBtn = bottomBtnJson;
    }
  }
};
</script>

<style lang="scss" scoped>
.operaitonContent {
  padding: 0.5rem;
  @include wh(100%, calc(100% - 8rem));
  position: absolute;
  overflow: scroll;
  .formLine {
    @include fr(flex-start);
    margin: 0.8rem 0rem;
    p:first-child {
      @include wh(7.5rem, auto);
      @include sc(0.9rem, $fColor);
      text-align: right;
    }
    input {
      border-radius: 0.2rem;
      border: 0.05rem solid $F5;
      // flex: 1;
      padding: 0.2rem 0.3rem;
    }
    .searchBtn {
      @include sc(0.8rem, $fc);
      border-radius: 0.3rem;
      background-color: $TBlue;
      padding: 0.3rem 0.5rem;
      margin-left: 0.5rem;
    }
  }
  .operationContent {
    background-color: $F5;
    padding: 0.5rem 0rem;
    div {
      @include fr(flex-start);
      i {
        margin-left: 0.5rem;
      }
    }
    .operationItem {
      @include fc(flex-start);
      @include wh(100%, auto);
      padding: 0.8rem 0.5rem;
      margin: 0.5rem 0.5rem;
      border-radius: 0.5rem;
      background: $fc;
      .topItem {
        @include fr(flex-start);
        @include wh(100%, auto);
        padding-bottom: 0.5rem;
        border-bottom: 0.02rem solid $F5;
        img {
          @include wh(2.3rem, 2.3rem);
          margin-right: 0.5rem;
        }
        .right {
          @include fCol(flex-start, flex-start);
          @include wh(100%, auto);
          @include sc(0.9rem, $fColor);

          .topInfo {
            @include fr(space-between);
            @include wh(100%, auto);
            @include sc(0.9rem, $line);
            p:first-child {
              @include sc(0.9rem, $fColor);
              font-weight: bold;
              margin-bottom: 0.2rem;
              span {
                @include sc(0.95rem, $yellow);
                margin-left: 0.3rem;
              }
            }
          }
        }
      }
      .rightBottomBtn {
        padding: 0.5rem 0rem 0rem 0rem;
        @include fr(flex-end);
        @include wh(100%, auto);
        .bottomBtn {
          @include wh(auto, 2rem);
          @include fr(center);
          @include sc(0.8rem, $fc);
          padding: 0.8rem 0.3rem;
          margin: 0rem 0.3rem;
          border-radius: 0.5rem;
          background-color: $TBlue;
        }
      }
    }
  }
}
.operationMean {
  @include grid(4, auto);
  @include sc(0.9rem, $fColor);
  @include wh(100%, auto);
  position: fixed;
  bottom: 0.5rem;
  z-index: 2;
  background-color: $fc;
  .mean {
    background-color: $fc;
    padding: 0.5rem;
    img {
      @include wh(2.5rem, 2.5rem);
      border: 0.2rem solid $F5;
      border-radius: 5rem;
    }
    p {
      margin-bottom: 0.3rem;
    }
  }
}
</style>